.rs-accordion-card{border-bottom:1px solid #ebebeb}.rs-accordion-card:last-child{border-bottom:0}.rs-accordion-card .rs-accordion-header{display:flex;align-items:flex-start;justify-content:space-between;font-size:1.2rem;font-weight:500;padding:24px 0;cursor:pointer}@media (min-width:992px){.rs-accordion-card .rs-accordion-header{align-items:center}}.rs-accordion-card .rs-accordion-header .rs-accordion-header-icon{display:flex;align-items:center;justify-content:center;width:14px;min-width:14px;max-width:14px;height:14px;min-height:14px;max-height:14px;transition:all .3s ease}.rs-accordion-card .rs-accordion-header .rs-accordion-header-icon svg{width:100%;height:100%}.rs-accordion-card .rs-accordion-header.collapsed .rs-accordion-header-icon{transform:rotate(180deg)}.rs-accordion-card .rs-accordion-body{display:none;height:0;transition:height .4s cubic-bezier(.31,.79,.35,.82);overflow:hidden;will-change:height}.rs-accordion-card .rs-accordion-body.show{display:block}.rs-accordion-card .rs-accordion-body .rs-accordion-content{padding-bottom:24px}